import React, { Component } from 'react';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';


export default class Optimal extends Component {
    constructor() {
        super();
        this.state = {
            stop: 37,
            size: 100,
            bestScore: 0,
            currentScore: 0,
            stopScore: -1,
            countSuccess: 0,
            countFailure: 0,
            countTotal: 0,
            data: this.generateArray(100),
            queue: [],
        }
        this.handleClickReset = this.handleClickReset.bind(this);
        this.handleClickResetCount = this.handleClickResetCount.bind(this);
        this.optimalStopping = this.optimalStopping.bind(this);
        this.startInterval = this.startInterval.bind(this);
        this.dequeue = this.dequeue.bind(this);
    }

    generateArray(n) {
        var arr = [];
        for (var i = 0; i < n; i ++) { arr[i] = i; }
        var temp, curr, top = arr.length;
        if (top) while(--top) {
            curr = Math.floor(Math.random() * (top + 1));
            temp = arr[curr];
            arr[curr] = arr[top];
            arr[top] = temp;
        }
        return arr;
    }

    handleInputChange = (event) => {
        this.setState({
            [event.target.name]: event.target.value
        });
    }

    handleClickReset() {
        this.stopInterval();
        const { size } = this.state;
        const data = this.generateArray(size);
        const queue = [];
        const bestScore = 0, currentScore = 0, stopScore = -1;
        this.setState({ data, queue, bestScore, currentScore, stopScore });
    }

    handleClickResetCount() {
        const countSuccess = 0, countFailure = 0, countTotal = 0;
        this.setState({ countSuccess, countFailure, countTotal });
    }

    optimalStopping() {
        const queue = [];
        const { data, stop } = this.state;
        let best = 0, stopIndex = parseInt(data.length * (stop / 100));
        for (var i = 0; i < data.length; i ++) {
            if (i < stopIndex) {
                if (data[i] > best) {
                    best = data[i];
                }
            } else {
                if (data[i] > best) {
                    queue.push([best, data[i], data[i]]);
                    this.setState({ queue});
                    this.startInterval();
                    return;
                }
            }
            queue.push([best, data[i], -1]);
        }
        this.setState({ queue });
        this.startInterval();
        return;
    }

    startInterval() {
        const intervalId = setInterval(() => {
            this.dequeue();
        }, 100);

        this.setState({ intervalId });
    }

    stopInterval() {
        clearInterval(this.state.intervalId);
    }

    dequeue() {
        const { queue } = this.state;
        if (queue.length) {
            const data = queue.shift();
            const bestScore = data[0], currentScore = data[1], stopScore = data[2];
            this.setState({ bestScore, currentScore, stopScore });
            return;
        }
        const { stopScore, countSuccess, countFailure, countTotal } = this.state;
        if (stopScore > 0) {
            this.setState({
                countSuccess: countSuccess + 1,
                countTotal: countTotal + 1,
            })
        } else {
            this.setState({
                countFailure: countFailure + 1,
                countTotal: countTotal + 1,
            })
        }
        this.stopInterval();
    }

    render() { 
        const { size, stop, bestScore, currentScore, stopScore, countSuccess, countFailure, countTotal } = this.state;

        return (
            <Card>
                <Card.Header>
                    <h3>Optimal Stopping</h3>
                </Card.Header>
                <Card.Body>
                    <Row>
                        <Col>
                            <Card >
                                <Card.Header className="optimal-card">Best Score</Card.Header>
                                <Card.Body className="optimal-card">{ bestScore }</Card.Body>
                            </Card>
                        </Col>
                        <Col>
                            <Card>
                                <Card.Header className="optimal-card">Current Score</Card.Header>
                                <Card.Body className="optimal-card">{ currentScore }</Card.Body>
                            </Card>
                        </Col>
                        <Col>
                            <Card>
                                <Card.Header className="optimal-card">Stop Score</Card.Header>
                                <Card.Body className="optimal-card">{ stopScore }</Card.Body>
                            </Card>
                        </Col>
                    </Row>
                    <br />
                    <Row>
                        <Col>
                            <Card>
                                <Card.Header className="optimal-card">Sucess</Card.Header>
                                <Card.Body className="optimal-card">{ countSuccess }</Card.Body>
                            </Card>
                        </Col>
                        <Col>
                            <Card>
                                <Card.Header className="optimal-card">Failure</Card.Header>
                                <Card.Body className="optimal-card">{ countFailure }</Card.Body>
                            </Card>
                        </Col>
                        <Col>
                            <Card>
                                <Card.Header className="optimal-card">Total</Card.Header>
                                <Card.Body className="optimal-card">{ countTotal }</Card.Body>
                            </Card>
                        </Col>
                    </Row>
                </Card.Body>
                <Card.Footer>
                    <Row>
                        <Form.Group as={Col}>
                            <Form.Label>Group Size: { size }</Form.Label>
                            <Form.Control name="size" type="range" min={10} max={1000} step={10} value={size} onChange={this.handleInputChange} />
                        </Form.Group>
                        <Form.Group as={Col}>
                            <Form.Label>Stopping Percentage: { stop }%</Form.Label>
                            <Form.Control name="stop" type="range" min={1} max={100} step={1} value={stop} onChange={this.handleInputChange} />
                        </Form.Group>
                        <Col>
                            <Button variant="dark" onClick={this.handleClickReset} block>New Group</Button>
                            <Button variant="dark" onClick={this.optimalStopping} block>Simulate</Button>
                        </Col>
                    </Row>
                    <br />
                    <Row>
                        <Button as={Col} variant="dark" onClick={this.handleClickResetCount}>Reset</Button>
                    </Row>
                </Card.Footer>
            </Card>
        );
    }
}